@import "./styles/fonts.scss";

.wrapper {
  background-image: url("./images/Bitmap.png");
  background-repeat: no-repeat;
  height: 300px;
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;
  background-color: #fafafa;
  height: 100vh;
}

.mask {
  position: absolute;
  background: linear-gradient(
    90deg,
    rgba(85, 150, 255, 1) 0%,
    rgba(109, 121, 250, 0.5702) 27.08%,
    rgba(121, 107, 247, 0.5784) 41.15%,
    rgba(147, 75, 241, 0.5135) 71.35%,
    rgba(159, 60, 238, 0.5542) 85.42%,
    rgba(172, 45, 235, 1) 100%
  );
  width: 100%;
  max-width: 1440px;
  height: 300px;
}

@media screen and (max-width: 1439px) {
  .wrapper,
  .mask {
    max-width: 900px;
  }
}

@media screen and (max-width: 899px) {
  .wrapper,
  .mask {
    max-width: 600px;
  }
}

@media screen and (max-width: 599px) {
  .wrapper,
  .mask {
    max-width: 375px;
  }
}

.todo {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 540px;
  margin: 0 auto;
}

.title {
  text-align: center;
  font-family: "Josefin Sans";
  padding-top: 70px;
}

.supplementary_menu {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  padding: 10px;
  max-width: 320px;
  width: 100%;
  margin-top: 16px;
  border-radius: 10px;
}
